import React from 'react';
import User from './User'


export default function Users () {
    return(
        <div className='right'>
            <User src='data:image/jpeg;base64,'
                  alt='man'
                  name='Scott'
            />
            <div className='users__block'>
                <User src='data:image/jpeg;base64,'
                      alt='man'
                      name='Scott'
                      min={true}
                />
                <User src='data:image/jpeg;base64,'
                      alt='man'
                      name='Scott'
                      min={true}
                />
                <User src='data:image/jpeg;base64,'
                      alt='man'
                      name='Scott'
                      min={true}
                />
                <User src='data:image/jpeg;base64,'
                      alt='man'
                      name='Scott'
                      min={true}
                />
            </div>
        </div>
    )
}